import React, {useState, useEffect } from 'react'
import { connect } from 'react-redux'
import {useHistory} from "react-router-dom";
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ButtonPermission from "components/permission-components/button.permission";
import Container from "components/layout-components/container";
import {DeleteOutlined, EyeOutlined} from '@ant-design/icons'
import ApiService from "../../../../../../../../../services/api.service";
import {Card, Table, message} from "antd";


function Overview(props){
    const history = useHistory();
    const [ data, setData ] = useState([])
    const [ loading, setLoading ] = useState(false)
    const [ loadings ,setLoadings] = useState([])
    const enterLoading = (index,value = true) => {
        setLoadings((prevLoadings) => {
            const newLoadings = [...prevLoadings];
            newLoadings[index] = value;
            return newLoadings;
        });

    };

    const [ params, setParams ] = useState({
        page:1,
        limit:10
    })
    const [ pagination, setPagination ] = useState({
        page:1,
        limit:10,
        total_items:0
    })

    useEffect(()=> {
        setLoading(true)
        new ApiService({
            url:`/api/v1/announcement/categories`,
            config : {
                params: {
                    ...params
                }
            }
        }).get()
            .then((response)=> {
                setData([...response?.data])
                setPagination({
                    ...pagination,
                    ...response?.pagination,
                    ...response?.paginate
                })
                setLoading(false)
            })
            .catch((err)=> {
                setData([])
                setLoading(false)
            })
    },[params])

    const onDelete = (id,index)=> {
        enterLoading(index,true)
        new ApiService({
            url:`/api/v1/announcement/categories/${id}`
        }).delete()
            .then((response)=> {
                message.info(response?.message ?? `Successfully: deleted by id ${id}`)
                setTimeout(()=> {
                    setData([...data.filter((item)=> item.id !== id)])
                    enterLoading(index,false)
                },2000)
                clearTimeout()

            })
            .catch((err)=> {
                message.error(err?.message ?? `Error: failed deleted by id ${id}`)
                enterLoading(index,false)
            })

    }


    const columns = [
        {
            dataIndex:"id",
            title:"No",
            render: (_,val,index)=> index + 1
        },
        {
            dataIndex:"name",
            title:"Name",
        },
        {
            dataIndex:"slug",
            title:"Slug",
        },
        {
            dataIndex:"description",
            title:"Description",
        },
        {
            render:(_,val,index)=> {
                return (
                    <div className={'text-right'}>
                        <ButtonPermission
                            permission={['admin']}
                            theme={{
                                type:"disabled",
                                property:{
                                    message:"Access Denied"
                                }
                            }}
                            config={{

                                type:loadings[index] ? "primary" : "default",
                                size:"small",
                                ghost:true,
                                danger:true,
                                loading:loadings[index] ?? false,
                                icon:<DeleteOutlined/>,
                                onClick: ()=> onDelete(_?.id,index)
                            }}/>
                    </div>
                )
            }
        }
    ]

    return (
        <>
            <HeroComponents
                loading={false}
                containerType={'primary'}
                name={"Overviews"}
                positionName={['General', 'Announcement','Category']}
                positionColor={'cyan'}
                thumbnail={null}
                extra={<>
                    <ButtonPermission
                        permission={['admin']}
                        theme={{
                            type:"disabled",
                            property:{
                                message:"Access Denied"
                            }
                        }}
                        config={{
                            type:"primary",
                            size:"small",
                            ghost:true,
                            onClick: function(){
                                history.push('/dashboard/general/announcement/category/create')
                            }
                        }}>
                        Add New
                    </ButtonPermission>
                </>}
            />

            <div style={{marginTop:90}}>
                <Container>
                    <Card bordered={false}>
                        <Table
                            columns={columns ?? []}
                            loading={loading ?? false}
                            dataSource={data ?? []}
                            pagination={pagination?.total_items < params?.limit ? false : {
                                pageSize: params?.limit,
                                total: pagination?.total_items ?? pagination?.total_items,
                                onChange: (page, pageSize) => {
                                    setParams({
                                        ...params,
                                        page,
                                        limit: pageSize
                                    })
                                }
                            }}
                        />
                    </Card>
                </Container>
            </div>

        </>
    )
}
Overview.propTypes= {}
Overview.defaultProps = {}
export default connect(()=> {
    return {}
},{})(React.memo(Overview))
